import { React, ReactDOM } from 'jimu-core';
import FontStyle from './font-style';
import FontColor from './font-color';
import Link from '../link/link';
import './css/style.scss';

interface Props {
    editorState: Draft.EditorState;
    onChange: any;
}

export default class EditPanel extends React.Component<Props, any> {
    constructor(props) {
        super(props);
    }

    render() {
        const { editorState } = this.props;
        return <div className="inline-font">
            <FontStyle editorState={editorState} onChange={this.props.onChange}></FontStyle>
            <FontColor editorState={editorState} onChange={this.props.onChange}></FontColor>
            <Link style={{marginLeft:'20px'}} editorState={editorState} onChange={this.props.onChange} ></Link>
        </div>
    }
}
