import {IMState} from 'jimu-core';
import {extensionSpec, ImmutableObject} from 'jimu-core';
import {publishAppConfigChangeToApp} from './builder-app-sync';

enum ActionKeys{
  InBuilderPutAppConfigIntoHistory = 'IN_BUILDER_PUT_APPCONFIG_INTO_HISTORY',
  InBuilderAppConfigUndo = 'IN_BUILDER_APPCONFIG_UNDO',
  InBuilderAppConfigRedo = 'IN_BUILDER_APPCONFIG_REDO'
}

declare module 'jimu-core/lib/types/state'{
  interface State{
    appStateHistory?: IMHistoryState
  }
}

declare module 'jimu-core'{
  interface State{
    appStateHistory?: IMHistoryState
  }
}

interface StateHistory{
  past: IMState[],
  future: IMState[]
}
export type IMHistoryState = ImmutableObject<StateHistory>

let actions = {

  InBuilderAppConfigUndo: () => {
    return {
      type: ActionKeys.InBuilderAppConfigUndo,
    };
  },

  InBuilderAppConfigRedo: () => {
    return {
      type: ActionKeys.InBuilderAppConfigRedo,
    };
  },
  InBuilderPutAppConfigIntoHistory: (appState) => {
    return {
      type: ActionKeys.InBuilderPutAppConfigIntoHistory,
      appState: appState
    }
  }
};

export {actions as appStateHistoryActions, ActionKeys as AppStateHistoryActionKeys}

export default class AppStateHistoryExtension implements extensionSpec.ReduxStoreExtension{
  id = 'app-state-history-extension'

  getActions(){
    return [
      ActionKeys.InBuilderPutAppConfigIntoHistory,
      ActionKeys.InBuilderAppConfigUndo,
      ActionKeys.InBuilderAppConfigRedo
    ];
  }

  getInitLocalState(){
    return {
      past: [],
      future: []
    };
  }

  getReducer(){
    return (historyState: IMHistoryState, action, builderState: IMState) => {
      switch(action.type){
        case ActionKeys.InBuilderAppConfigUndo:
          return this.handleAppStateUndo(historyState);
        case ActionKeys.InBuilderAppConfigRedo:
          return this.handleAppStateRedo(historyState);
        case ActionKeys.InBuilderPutAppConfigIntoHistory:
          return this.handleAppStateAdd(historyState, action.appState);
        default:
          return historyState;
      }
    }
  }
  handleAppStateUndo(state){
    // last member in `past` array is the current appState
    if(state.past.length < 2){
      console.warn('Can not undo since no history there');
      return state;
    }
    const previous = state.past[state.past.length - 2];
    const current = state.past[state.past.length - 1];
    const past = state.past.slice(0, state.past.length - 1);
    const future = [current].concat(state.future);

    publishAppConfigChangeToApp(previous);

    return state.merge({
      'past': past,
      'future': future
    });
  }

  handleAppStateRedo(state){
    // last member in `past` array is the current appState
    if(state.future.length < 1){
      console.warn('Can not redo since no history there');
      return state;
    }
    const next = state.future[0];
    const future = state.future.slice(1);
    const past = state.past.concat([next]);

    publishAppConfigChangeToApp(next);

    return state.merge({
      'past': past,
      'future': future
    });
  }

  handleAppStateAdd(state, appState){
    // if appState is not generated by redo or undo or `past` is emperty
    if(appState !== null && ((state.past.length > 0 && state.past[state.past.length - 1] !== appState) || state.past.length === 0)){
      const past = state.past.concat([appState]);
      return state.set('past', past);
    }
    return state;
  }

  getStoreKey(){
    return 'appStateHistory';
  }
}