import { React } from 'jimu-core';

export default class ColorButton extends React.Component<any, any> {
    onSelected: any;

    constructor(props) {
        super(props);
        this.onSelected = (e, color, style) => {
            e.preventDefault();
            e.nativeEvent.stopImmediatePropagation();
            this.props.onSelected(color, style);
        };
    }
    render() {
        const { color, active, style } = this.props;
        let className = 'color-item';
        if (active) {
            className += ' active';
        }
        return (
            <div className={className}>
                <span style={{ backgroundColor: color }} className="color-block" onClick={(e) => this.onSelected(e, color, style)}></span>
            </div>
        );
    }
}