import { React, ReactDOM } from 'jimu-core';
import { colorStyles } from './colors';
import { Popover, PopoverBody } from 'jimu-ui'
import ColorButton from './color-button'


interface Props {
  target: HTMLDivElement;
  container: HTMLDivElement;
  show: boolean;
  onSelected: any;
  editorState: Draft.EditorState;
}
export default class InlineStyleControls extends React.Component<Props, any> {
  constructor(props) {
    super(props);
  }

  hasNoneColor(currentStyle) {
    let hasOneColor = colorStyles.some((item) => {
      return currentStyle.has(item.style);
    });
    return !hasOneColor
  }

  calculateActive(currentStyle, style, i) {
    return currentStyle.has(style);
  }

  render() {
    const { editorState, target, container, show, onSelected } = this.props;
    var currentStyle = editorState.getCurrentInlineStyle();
    return ReactDOM.createPortal(
      <Popover container={container || null} placement="bottom" isOpen={show} target={target} hideArrow={true}>
        <PopoverBody>
          <div className="color-panel">
            {
              colorStyles.map((item, i) =>
                <ColorButton active={this.calculateActive(currentStyle, item.style, i)} editorState={editorState} style={item.style} color={item.color} key={i} onSelected={onSelected} />
              )
            }
          </div>
        </PopoverBody>
      </Popover>,
      container
    );


  }
}
