import {React, classNames as classnames} from 'jimu-core';
import {BSCard, CardImg, CardBody, CardTitle, CardSubtitle, CardText, Modal, ModalHeader, ModalBody, ModalFooter, Button} from 'jimu-ui';
import './css/choose-widget-popup.scss';

interface CardImage{
  src: string;
  alt?: string;
}
export interface WidgetCardType{
  title: string;
  uri: string;
  image: CardImage;
}
interface Props{
  title?: string;
  onChange?: (listItem) => void;
  onOK: (widgetName: string) => void
  onCancel?: () => void
}
interface State{
  currentWidget: string;
}
export class ChooseWidgetPopup extends React.PureComponent<Props, State>{

  constructor(props) {
    super(props);
    this.state = {currentWidget: null}
  }

  listInfo: WidgetCardType[] = this.getWidgetListInfo()

  onSelectWidget = (widgetUri: string) => {
    this.setState({currentWidget: widgetUri});
  }

  getWidgetListInfo(){
    const widgetInfo = require('../../../widgets/widgets-info.json');
    const listInfo = widgetInfo.map(v => {
      let tempInfo:WidgetCardType = {
        title: '',
        uri: '',
        image: {
          src: ''
        }
      };

      tempInfo.title = v.label;
      tempInfo.uri = v.path;
      tempInfo.image.src = '../' + v.icon;

      return tempInfo;
    });
    return listInfo;
  }

  getListItemJSX = (item: WidgetCardType) => {
    return (
      <BSCard className={classnames('widget-card-item', {'widget-card-selected':
      this.state.currentWidget && this.state.currentWidget.indexOf(item.uri) === 0})} key={item.title}
        onClick={() => this.onSelectWidget(item.uri)}>
        <div className='widget-card-image'>
          <div>
            <CardImg top width='100%' src={item.image.src} alt={item.image.alt} />
          </div>
        </div>
        <CardBody className='widget-card-content'>
        <span className='text-capitalize' title={item.title}>{item.title}</span>
        </CardBody>
      </BSCard>
    );
  }

  render(){
    return(
      <div className='component-choose-widget-popup'>
        <Modal isOpen={true} className='component-choose-widget-popup-modal'>
          <ModalHeader className='component-choose-widget-clear-border component-choose-widget-modal-header'>
            {this.props.title}
            <span className='choose-widget-popup-close' onClick={this.props.onCancel}></span>
          </ModalHeader>
          <ModalBody className='component-choose-widget-clear-border component-choose-widget-modal-body'>
            {this.listInfo.map(l => this.getListItemJSX(l))}
          </ModalBody>
          <ModalFooter className='component-choose-widget-clear-border component-choose-widget-modal-footer'>
            <Button onClick={() => this.props.onOK(this.state.currentWidget)}>OK</Button>
            <Button onClick={this.props.onCancel}>Cancel</Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}
