import * as React from 'react';
import {connect} from 'react-redux';
import isDeepEqual = require('fast-deep-equal');

import {IMWidgetJson, store, amdRequire, I18NManager, IMState} from 'jimu-core';
import {builderActions} from './builder-state-store-extension';
import {WidgetSettingProps, WidgetSettingInjectedProps} from './props';

interface WidgetRawSettingClassInfo{
  clazz: any;
  nls: any
}
export default class WidgetSettingManager{

  static instance: WidgetSettingManager;
  static getInstance(){
    if(!WidgetSettingManager.instance){
      WidgetSettingManager.instance = new WidgetSettingManager();
      window._widgetManager = WidgetSettingManager.instance;
    }
    return WidgetSettingManager.instance;
  }

  widgetSettingClasses: {
    [widgetId: string]: React.ComponentClass<WidgetSettingProps>
  } = {};

  widgetRawSettingClasses: {
    [widgetName: string]: Promise<WidgetRawSettingClassInfo>
  } = {};


  getWidgetSettingClass(widgetId: string){
    return this.widgetSettingClasses[widgetId]
  }

  deleteWidgetClass(widgetId: string){
    delete this.widgetSettingClasses[widgetId];
  }

  loadWidgetSettingClass(widgetId: string): Promise<React.ComponentClass<WidgetSettingProps>>;
  loadWidgetSettingClass(widgetJson: IMWidgetJson): Promise<React.ComponentClass<WidgetSettingProps>>;
  loadWidgetSettingClass(w): any{
    let widgetJson: IMWidgetJson;
    if(typeof w === 'string'){
      widgetJson = store.getState().appState.appConfig.widgets[w];
    }else{
      widgetJson = w;
    }

    let wrapWidgetSettingClass = (_WidgetClass, nls) => {
      var WidgetClass = _WidgetClass.default? _WidgetClass.default: _WidgetClass;

      let WidgetContextClass = this.injectWidgetProps(WidgetClass, nls, widgetJson);
      return WidgetContextClass;
    };

    return this.loadRawSettingClass(widgetJson).then( classInfo => {
      return wrapWidgetSettingClass(classInfo.clazz, classInfo.nls);
    }).then((widgetClass: React.ComponentClass<WidgetSettingProps>) => {
      this.widgetSettingClasses[widgetJson.id] = widgetClass;
      store.dispatch(builderActions.widgetSettingClassLoaded(widgetJson.id));
      return widgetClass;
    });
  }

  loadRawSettingClass(widgetJson: IMWidgetJson): Promise<WidgetRawSettingClassInfo>{
    let widgetName = widgetJson.manifest.name;
    if(this.widgetRawSettingClasses[widgetName]){
      return this.widgetRawSettingClasses[widgetName];
    }

    let uri =  `${widgetJson.uri}/dist/setting/setting`;
    if(!widgetJson.manifest.properties.hasSettingPage){
      uri = `jimu-for-builder/json-editor-setting`;
    }
    this.widgetRawSettingClasses[widgetName] = amdRequire(uri).then( WidgetClass => {
      if(widgetJson.manifest.properties.hasSettingPage && widgetJson.manifest.properties.hasSettingLocale){
        return this.loadWidgetSettingNls(widgetJson).then(nls => {
          return {
            clazz: WidgetClass,
            nls: nls
          }
        });
      }else{
        return {
          clazz: WidgetClass,
          nls: {}
        };
      }
    })

    return this.widgetRawSettingClasses[widgetName];
  }

  loadWidgetSettingNls(widgetJson: IMWidgetJson): Promise<any>{
    return I18NManager.getInstance().loadNls(`${widgetJson.context.amdFolder}dist/setting/nls/strings`);
  }

  injectWidgetProps(WrappedComponent, nlsStrings, widgetJson: IMWidgetJson): React.ComponentClass<WidgetSettingProps> {
    class WidgetWrapper extends React.PureComponent<WidgetSettingProps & WidgetSettingInjectedProps<{}>, any> {
      static displayName = `WidgetSettingWrapper(${widgetJson.manifest.name})`;
  
      constructor(props) {
        super(props);
      }
  
      render() {
        let injectProps:any = {
          nls: nlsStrings,
        };
  
        return (<WrappedComponent {...this.props} {...injectProps}/>);
      }
    }
  
    //copy static properites
    for(let p in WrappedComponent){
      if(!WidgetWrapper[p]){
        WidgetWrapper[p] = WrappedComponent[p];
      }
    }
  
    const mapStateToProps = (state: IMState) => {
      let props: any = {
        queryObject: state.queryObject,
        dataSourceStatus: {}
      };

      widgetJson.useDataSources && widgetJson.useDataSources.forEach(dsId => {
        props.dataSourceStatus[dsId] = state.dataSourcesInfo[dsId] && state.dataSourcesInfo[dsId].status;
      });
      
      return Object.assign(props, state.appState.appConfig.widgets[widgetJson.id]);
    };
    return connect(mapStateToProps)(WidgetWrapper) as React.ComponentClass<WidgetSettingProps>;
  }
}
