import { React } from 'jimu-core';
import { Popover, PopoverBody, InputGroup, Input, InputGroupAddon, InputGroupText } from 'jimu-ui'
import { EditorState, RichUtils } from 'draft-js';
import { Icon } from 'jimu-ui';
import { getSelectedText, isDescendant } from '../../../utils'
import './css/style.scss';

interface Props {
    editorState: Draft.EditorState;
    onChange: (EditorState: Draft.EditorState) => void;
    style: object;
}

interface State {
    showURLInput: boolean;
    urlValue: string;
}

export default class Link extends React.Component<Props, State> {
    linkNode: HTMLDivElement;
    handler: any;
    constructor(props) {
        super(props);
        this.state = {
            showURLInput: false,
            urlValue: ''
        };
    }

    componentDidMount() {
        this.handler = (e) => {
            e.stopPropagation();
            if (!this.linkNode) {
                return;
            }
            let target = e.target;
            if (!isDescendant(target, this.linkNode)) {
                this.hideURLInput();
            }
        }
        document.body.addEventListener('mousedown', this.handler);
    }

    componentWillUnmount() {
        document.body.removeEventListener("mousedown", this.handler);
        this.handler = null;
    }

    toggleLinkPanel = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        evt.preventDefault();
        this.setState({ showURLInput: !this.state.showURLInput })
        this.promptLink();
    }

    hideURLInput = () => {
        this.setState({ showURLInput: false })
    }

    onUrlInputChange = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        let urlValue = evt.target.value;
        this.setState({ urlValue });

    }

    promptLink = () => {
        let urlValue = this.getUrlValue();
        this.setState({ urlValue });
    }

    getUrlValue = () => {
        const { editorState } = this.props;
        let urlValue = '';
        if (!editorState) {
            return urlValue;
        }
        const selection = editorState.getSelection();
        if (selection.isCollapsed()) {
            return urlValue;
        }
        const contentState = editorState.getCurrentContent();
        const startKey = editorState.getSelection().getStartKey();
        const startOffset = editorState.getSelection().getStartOffset();
        const blockWithLinkAtBeginning = contentState.getBlockForKey(startKey);
        const linkKey = blockWithLinkAtBeginning.getEntityAt(startOffset);

        if (linkKey) {
            const linkInstance = contentState.getEntity(linkKey);
            urlValue = linkInstance.getData().url;
        }
        return urlValue;
    }

    hasLink = () => {
        return !!this.getUrlValue();
    }

    hasSelection = () => {
        const { editorState } = this.props;
        return !!getSelectedText(editorState)
    }

    confirmLink = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        evt.preventDefault();
        const { editorState } = this.props;
        const { urlValue } = this.state;
        if (!urlValue) {
            return;
        }
        const contentState = editorState.getCurrentContent();
        const contentStateWithEntity = contentState.createEntity(
            'LINK',
            'MUTABLE',
            { url: urlValue }
        );
        const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
        let newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity });
        newEditorState = RichUtils.toggleLink(
            newEditorState,
            newEditorState.getSelection(),
            entityKey
        );
        this.props.onChange(newEditorState);
        this.hideURLInput();
    }

    removeLink = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        evt.preventDefault();
        const { urlValue } = this.state;
        if (!urlValue) {
            return;
        }
        const { editorState } = this.props;
        const selection = editorState.getSelection();
        if (!selection.isCollapsed()) {
            let newEditorState = RichUtils.toggleLink(editorState, selection, null);
            this.props.onChange(newEditorState)
        }
        this.hideURLInput();
    }

    onLinkInputKeyDown = (e) => {
        if (e.which === 13) {
            this.confirmLink(e);
        }
    }

    render() {
        const { showURLInput } = this.state;
        const { urlValue } = this.state;
        const hasLink = this.hasLink();
        const hasSelection = this.hasSelection();

        let linkPanel = <div></div>;

        if (this.linkNode) {
            linkPanel = <Popover placement="bottom-end" isOpen={showURLInput} target={this.linkNode} container={this.linkNode} hideArrow={false}>
                <PopoverBody>
                    <InputGroup className="url-input-panel" size="sm">
                        <Input type="url" value={urlValue} placeholder="https://example.com/" onChange={this.onUrlInputChange} onKeyDown={this.onLinkInputKeyDown} />
                        <InputGroupAddon addonType="append">
                            <InputGroupText className="operator-btn" onMouseDown={this.removeLink}>x</InputGroupText>
                            <InputGroupText className="operator-btn" onMouseDown={this.confirmLink}>√</InputGroupText>
                        </InputGroupAddon>
                    </InputGroup>
                </PopoverBody>
            </Popover>;
        }


        return <div style={this.props.style} className="inline-link" ref={(rf) => this.linkNode = rf}>
            {linkPanel}
            <div className="link-btn" onClick={this.toggleLinkPanel}><Icon icon={hasSelection ? hasLink ? 'link-horizontal' : 'unlocked-link-horizontal' : 'link'} /></div>
        </div>
    };
}
