import {React, AjaxState, SeamlessImmutable, ImmutableObject} from 'jimu-core';
import {Tab, Tabs} from 'jimu-ui';
import { ImmutableArray } from 'jimu-core';
import ItemChooser from './item-chooser';

export interface DataSourceInfo{
  type: string;

  url?: string;
  itemId?: string;
  itemTitle?: string;
}
interface Props{
  portalUrl: string;
  onDataSourceSelect?: (dataSourceInfo: DataSourceInfo) => void
}
export default class ExternalDataSourceChooser extends React.PureComponent<Props, {}>{
  constructor(props){
    super(props);
  }
  componentDidMount(){
  }

  render(){
    return <Tabs>
      <Tab title="Webmap" active={true}>
        <ItemChooser portalUrl={this.props.portalUrl} itemType="Web Map"
          onSelect={item => this.props.onDataSourceSelect && this.props.onDataSourceSelect({type: 'WEB_MAP', itemId: item.id, itemTitle: item.title})}/>
      </Tab>
    </Tabs>;
  }
}