import { React, ReactDOM } from 'jimu-core';
import FontItem from './font-style-item';
import { toggleInlineStyle } from '../../../utils';

interface Props {
    editorState: Draft.EditorState;
    onChange: any;
}

const FontStyles = [
    { icon: "b-icon", style: 'BOLD' },
    { icon: "i-icon", style: 'ITALIC' },
    { icon: "u-icon", style: 'UNDERLINE' },
    { icon: "s-icon", style: 'STRIKE' },
];

export default class FontStyle extends React.Component<Props, any> {
    catchBlockType: string;
    constructor(props) {
        super(props);
    }

    onFontStyleChange = (type) => {
        const { editorState } = this.props;
        let nextEditorState = toggleInlineStyle(type, editorState);
        this.props.onChange(nextEditorState);
    }

    render() {
        const { editorState } = this.props;
        const currentStyle = editorState.getCurrentInlineStyle();
        const ItemNum = FontStyles.length;
        return <div className="font-style">
            {FontStyles.map((item, index) =>
                <FontItem
                    key={index}
                    active={currentStyle.has(item.style)}
                    icon={item.icon}
                    style={item.style}
                    rightBorder={index !== ItemNum - 1}
                    onChange={this.onFontStyleChange}
                />
            )}
        </div>
    }
}
