export const colorStyles = colors.map(c => {
  return {
    color: c,
    style: `color-${c.replace('#', '')}`
  }
});

const draftColorStylesMap = {};

colors.forEach((c, i) => {
  draftColorStylesMap[`color-${c.replace('#', '')}`] = { color: c }
});

export const colorStyleMap = draftColorStylesMap;

const htmlColorStylesMap = {};
colors.forEach((c, i) => {
  htmlColorStylesMap[`color-${c.replace('#', '')}`] = { style: { color: c } };
});

export const htmlcolorStyleMap = htmlColorStylesMap;