import { React, ReactDOM } from 'jimu-core';
import { toggleBlockType, getBlockType } from '../../utils';
import { Input } from 'jimu-ui';
import './css/style.scss';

interface Props {
    editorState: Draft.EditorState;
    onChange: any;
}

export default class HeadersSelect extends React.Component<Props, any> {
    catchBlockType: string;
    constructor(props) {
        super(props);
    }

    onQuicyStyleChange = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        let blockType = evt.target.value;
        this.catchBlockType = blockType;
        if (blockType === 'normal') {
            blockType = this.catchBlockType;
        }
        const { editorState } = this.props;
        let nextEditorState = toggleBlockType(blockType, editorState);
        this.props.onChange(nextEditorState);
    }


    getBlockType(editorState) {
        if (!editorState) {
            return "normal";
        }
        let blockType = getBlockType(editorState);
        return blockType || 'normal';
    }

    render() {
        const { editorState } = this.props;
        let blockType = this.getBlockType(editorState);
        return <Input value={blockType} onChange={this.onQuicyStyleChange} className="headers-select" type="select" name="headers-select">
            <option value="normal">Normal</option>
            <option value="header-one">Heading 1</option>
            <option value="header-two">Heading 2</option>
            <option value="header-three">Heading 3</option>
            <option value="header-four">Heading 4</option>
            <option value="header-five">Heading 5</option>
            <option value="header-six">Heading 6</option>
        </Input>
    }
}
