import { React, ReactDOM } from 'jimu-core';
import ColorPicker from '../../color/picker';
import { getColorByEditorState, toggleColor } from '../../../utils';

interface Props {
    editorState: Draft.EditorState;
    onChange: any;
}


export default class FontColor extends React.Component<Props, any> {
    colorPickerNode: any;
    constructor(props) {
        super(props);
    }

    onFontColorChange = (type) => {
        const { editorState } = this.props;
        let nextEditorState = toggleColor(type, editorState);
        this.props.onChange(nextEditorState);
    }

    onSelected = (color, style) => {
        this.setState({ color });
        this.onFontColorChange(style);
    }

    onRightClick = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        if (this.colorPickerNode) {
            this.colorPickerNode.toggleColorPanel();
        }
    }

    render() {
        const { editorState } = this.props;
        const color = getColorByEditorState(editorState);

        return <div className="font-color">
            <div className="left" style={{ borderColor: color }}>
                <ColorPicker ref={ref => this.colorPickerNode = ref} editorState={editorState} onSelected={this.onSelected} />
            </div>
            <div className="right" onClick={this.onRightClick}></div>
        </div>
    }
}
