import {AppCreationInfo} from 'jimu-core';
import {AppConfig, IMAppConfig, appConfigUtils, SessionManager} from 'jimu-core';
import {searchItems, removeItem, updateItem, createItem} from '@esri/arcgis-rest-items';
import {IItem} from '@esri/arcgis-rest-common-types';

export function createApp(appInfo: AppCreationInfo): Promise<IItem>{
  return Promise.all([getTemplateConfig(appInfo)])
  .then(function(results){
    var defaultAppConfig = results[0] as AppConfig;
    return createAppItem(appInfo, defaultAppConfig, true);
  });
}

export function searchApp(): Promise<IItem[]>{
  let session = SessionManager.getInstance().getSession();
  return searchItems({
    searchForm: {
      q: `typekeywords: "EXB Experience" AND owner:${session.username}`, 
    },
    authentication: session}
  ).then(result => {
    return Promise.resolve(result.results);
  });
}

export function deleteApp(appId: string): Promise<void>{
  return removeItem({
    id: appId,
    authentication: SessionManager.getInstance().getSession()
  });
}

export function saveApp(appId: string, appConfig: IMAppConfig): Promise<void>{
  let session = SessionManager.getInstance().getSession();
  return updateItem({
    item: {
      id: appId,
      owner: session.username,
      data: appConfigUtils.getCleanAppConfig(appConfig)
    },
    authentication: SessionManager.getInstance().getSession()
  }).then(() => {
    return;
  });
}

function createAppItem(appInfo: AppCreationInfo, appConfig: AppConfig, ignoreDuplicatedTitle: boolean): Promise<IItem> {
  var itemInfo = {
    title: appInfo.name,
    type: "Web Mapping Application",
    typeKeywords: ['EXB Experience', 'Ready To Use', 'JavaScript'],
    snippet: appInfo.description,
    tags: appInfo.tags,
    data: appConfig,
    // thumbnailUrl:appInfo.thumbnailUrl
  };

  let checkExist = () => {
    return Promise.resolve(false);
  };
  return checkExist().then(function(exist) {
    if(exist){
      return Promise.reject('app existed.');
    }
    return createItem({
      item: itemInfo,
      authentication: SessionManager.getInstance().getSession()
    });
  });
}


function getTemplateConfig(appInfo: AppCreationInfo): Promise<AppConfig>{
  return fetch(`../templates/${appInfo.template}/config.json`)
  .then(res => res.json())
  .then(appJson => {
    return appJson;
  });
}
