import * as SeamlessImmutable from 'seamless-immutable';
import {ImmutableObject, ImmutableArray} from "seamless-immutable";

import {WidgetJson, IMAppConfig, DataSourceJson, IMAbstractContainerJson, appConfigUtils, extensionSpec} from 'jimu-core';
import {State, IMState} from 'jimu-core';
import {publishAppConfigChangeToApp, publishAppModeChangeToApp} from './builder-app-sync';
import { IMLayoutJson } from 'jimu-core';
import * as appConfigActions from './app-config-actions';

declare module 'jimu-core/lib/types/state'{
  interface State{
    appState?: IMState
  }
}

declare module 'jimu-core'{
  interface State{
    appState?: IMState
  }
}

export enum ExtActionKeys{
  //actions from app
  InAppAppStateChanged = 'IN_APP_APP_STATE_CHANGED',
}

export interface InAppAppStateChangeAction{
  type: ExtActionKeys.InAppAppStateChanged;
  appState: IMState;
}

const actions = Object.assign({}, appConfigActions.actions, {
  inAppAppStateChanged: (appState: IMState) => {
    return {
      type: ExtActionKeys.InAppAppStateChanged,
      appState: appState
    };
  },
})

// export type ActionKeys = appConfigActions.ActionKeys | ExtActionKeys;
type ActionTypes = appConfigActions.ActionTypes | InAppAppStateChangeAction;

export {ActionTypes as AppStateActionTypes, actions as appStateActions}

export default class BuilderAppStateReduxStoreExtension implements extensionSpec.ReduxStoreExtension{
  id = 'builder-app-state-redux-store-extension';

  getActions(){
    return Object.keys(appConfigActions.ActionKeys).map(k => appConfigActions.ActionKeys[k]).concat(Object.keys(ExtActionKeys).map(k => ExtActionKeys[k]));
  }

  getInitLocalState(){
    return null;
  }

  getReducer(){
    return (appState: IMState, action: ActionTypes, builderState: IMState): IMState => {
      let s, appConfig: IMAppConfig;
      switch(action.type){
        case appConfigActions.ActionKeys.InBuilderEditAppEditWidget:
          appConfig = this.handleEditWidget(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppAddWidget:
          appConfig = this.handleAddWidget(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppRemoveWidget:
          appConfig = this.handleRemoveWidget(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case appConfigActions.ActionKeys.InBuilderEditAppAddPage:
          appConfig = this.handleAddPage(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppRemovePage:
          appConfig = this.handleRemovePage(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppEditPage:
          appConfig = this.handleEditPage(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case appConfigActions.ActionKeys.InBuilderEditAppAddSection:
          appConfig = this.handleAddSection(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppRemoveSection:
          appConfig = this.handleRemoveSection(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case appConfigActions.ActionKeys.InBuilderEditAppAddView:
          appConfig = this.handleAddView(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppRemoveView:
          appConfig = this.handleRemoveView(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppEditView:
          appConfig = this.handleEditView(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case appConfigActions.ActionKeys.InBuilderEditAppAddDataSource:
          appConfig = this.handleAddDataSource(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);
        case appConfigActions.ActionKeys.InBuilderEditAppRemoveDataSource:
          appConfig = this.handleRemoveDataSource(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case appConfigActions.ActionKeys.InBuilderEditAppAddDataSource:
          let dataSources = appState.appConfig.dataSources? appState.appConfig.dataSources.merge({[action.dataSourceJson.id]: action.dataSourceJson}):
            {[action.dataSourceJson.id]: action.dataSourceJson};

          s = appState.setIn(['appConfig', 'dataSources'], dataSources);
          publishAppConfigChangeToApp(s.appConfig);
          return s;

        case appConfigActions.ActionKeys.InBuilderEditAppEditAttributes:
          appConfig = this.handleEditAttributes(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case appConfigActions.ActionKeys.InBuilderEditAppEditSkin:
          appConfig = this.handleEditSkin(appState.appConfig, action);
          publishAppConfigChangeToApp(appConfig);
          return appState.set('appConfig', appConfig);

        case ExtActionKeys.InAppAppStateChanged:
          return action.appState;

        default:
          return appState;
      }
    }
  }

  handleAddWidget(appConfig: IMAppConfig, action: appConfigActions.AddWidgetAction): IMAppConfig{
    if(appConfig.widgets[action.widgetJson.id]){
      return appConfig;
    }

    let info = appConfigUtils.getContainerInfoByLayoutId(appConfig, action.layoutInfo.layoutId);

    let widgets;
    if(appConfig[info.containerType][info.containerId] && appConfig[info.containerType][info.containerId].widgets){
      widgets = (appConfig[info.containerType][info.containerId].widgets as ImmutableArray<string>).asMutable();
      widgets.push(action.widgetJson.id);
    }else{
      widgets = [action.widgetJson.id];
    }
    return appConfig.setIn(['widgets', action.widgetJson.id], action.widgetJson)
      .setIn([info.containerType, info.containerId, 'widgets'], SeamlessImmutable(widgets));
  }

  handleRemoveWidget(appConfig: IMAppConfig, action: appConfigActions.RemoveWidgetAction): IMAppConfig{
    if(!appConfig.widgets[action.widgetId]){
      return appConfig;
    }

    let info = appConfigUtils.getWidgetContainerInfo(appConfig, action.widgetId);
    if(!appConfig[info.containerType][info.containerId] ||
      appConfig[info.containerType][info.containerId].widgets.indexOf(action.widgetId) < 0){
      return appConfig;
    }
    let widgets = (appConfig[info.containerType][info.containerId].widgets as ImmutableArray<string>)
      .flatMap(wId => wId === action.widgetId? []: [wId]);
    
    if(appConfig.widgets[action.widgetId].outputDataSources){
      appConfig = appConfig.set('dataSources', appConfig.dataSources.without.apply(appConfig.dataSources, appConfig.widgets[action.widgetId].outputDataSources));
    }

    return appConfig.set('widgets', appConfig.widgets.without(action.widgetId))
      .setIn([info.containerType, info.containerId, 'widgets'], widgets);
  }

  handleEditWidget(appConfig: IMAppConfig, action: appConfigActions.EditWidgetAction): IMAppConfig{
    return appConfig.setIn(['widgets', action.widgetJson.id], action.widgetJson);
  }

  handleAddPage(appConfig: IMAppConfig, action: appConfigActions.AddPageAction): IMAppConfig{
    if(appConfig.pages && appConfig.pages[action.pageJson.id]){
      return appConfig;
    }

    appConfig = appConfig.setIn(['pages', action.pageJson.id], action.pageJson);
    if(action.pageLayoutJson){
      appConfig = this._addLayout(appConfig, action.pageLayoutJson);
    }
    return appConfig;
  }

  handleRemovePage(appConfig: IMAppConfig, action: appConfigActions.RemovePageAction): IMAppConfig{
    if(!appConfig.pages || !appConfig.pages[action.pageId]){
      return appConfig;
    }

    appConfig = this._removePageContent(appConfig, action.pageId);

    return appConfig.set('pages', appConfig.pages.without(action.pageId));
  }

  handleEditPage(appConfig: IMAppConfig, action: appConfigActions.EditPageAction): IMAppConfig{
    return appConfig.setIn(['pages', action.pageJson.id], action.pageJson);
  }

  handleAddSection(appConfig: IMAppConfig, action: appConfigActions.AddSectionAction): IMAppConfig{
    if(appConfig.sections && appConfig.sections[action.sectionJson.id]){
      return appConfig;
    }
    let info = appConfigUtils.getContainerInfoByLayoutId(appConfig, action.layoutInfo.layoutId);

    let sections;
    if(appConfig[info.containerType][info.containerId] && appConfig[info.containerType][info.containerId].sections){
      sections = (appConfig[info.containerType][info.containerId].sections as ImmutableArray<string>).asMutable();
      sections.push(action.sectionJson.id);
    }else{
      sections = [action.sectionJson.id];
    }
    return appConfig.setIn(['sections', action.sectionJson.id], action.sectionJson)
      .setIn([info.containerType, info.containerId, 'sections'], SeamlessImmutable(sections));
  }

  handleRemoveSection(appConfig: IMAppConfig, action: appConfigActions.RemoveSectionAction): IMAppConfig{
    if(!appConfig.sections || !appConfig.sections[action.sectionId]){
      return appConfig;
    }

    let info = appConfigUtils.getSectionContainerInfo(appConfig, action.sectionId);
    if(!appConfig[info.containerType][info.containerId] ||
      appConfig[info.containerType][info.containerId].sections.indexOf(action.sectionId) < 0){
      return appConfig;
    }
    let sections = (appConfig[info.containerType][info.containerId].sections as ImmutableArray<string>)
      .flatMap(sId => sId === action.sectionId? []: [sId]);

    appConfig = this._removeSectionContent(appConfig, action.sectionId);

    return appConfig.set('sections', appConfig.sections.without(action.sectionId))
      .setIn([info.containerType, info.containerId, 'sections'], sections);
  }

  handleAddView(appConfig: IMAppConfig, action: appConfigActions.AddViewAction): IMAppConfig{
    if(appConfig.views && appConfig.views[action.viewJson.id]){
      return appConfig;
    }
    let views;
    if(appConfig.sections && appConfig.sections[action.sectionId] && appConfig.sections[action.sectionId].views){
      views = appConfig.sections[action.sectionId].views.asMutable();
      views.push(action.viewJson.id);
    }else{
      views = [action.viewJson.id];
    }

    appConfig = appConfig.setIn(['views', action.viewJson.id], action.viewJson)
      .setIn(['sections', action.sectionId, 'views'], SeamlessImmutable(views));

    if(action.viewLayoutJson){
      appConfig = this._addLayout(appConfig, action.viewLayoutJson);
    }
    return appConfig;
  }

  handleRemoveView(appConfig: IMAppConfig, action: appConfigActions.RemoveViewAction): IMAppConfig{
    if(!appConfig.views || !appConfig.views[action.viewId]){
      return appConfig;
    }

    if(!appConfig.sections || !appConfig.sections[action.sectionId] || 
      appConfig.sections[action.sectionId].views.indexOf(action.viewId) < 0){
      return appConfig;
    }
    let views = appConfig.sections[action.sectionId].views
      .flatMap(vId => vId === action.viewId? []: [vId]);
    
    appConfig = this._removeViewContent(appConfig, action.viewId);

    return appConfig.set('views', appConfig.views.without(action.viewId))
      .setIn(['sections', action.sectionId, 'views'], views);
  }

  handleEditView(appConfig: IMAppConfig, action: appConfigActions.EditViewAction): IMAppConfig{
    return appConfig.setIn(['views', action.viewJson.id], action.viewJson);
  }

  handleAddDataSource(appConfig: IMAppConfig, action: appConfigActions.AddDataSourceAction): IMAppConfig{
    if(appConfig.dataSources && appConfig.dataSources[action.dataSourceJson.id]){
      return appConfig;
    }

    return appConfig.setIn(['dataSources', action.dataSourceJson.id], action.dataSourceJson);
  }

  handleRemoveDataSource(appConfig: IMAppConfig, action: appConfigActions.RemoveDataSourceAction): IMAppConfig{
    if(!appConfig.dataSources || !appConfig.dataSources[action.dataSourceId]){
      return appConfig;
    }
    return appConfig.set('dataSources', appConfig.dataSources.without(action.dataSourceId));
  }

  handleEditAttributes(appConfig: IMAppConfig, action: appConfigActions.EditAttributesAction){
    return appConfig.setIn(['attributes'], action.attributesJson);
  }

  handleEditSkin(appConfig: IMAppConfig, action: appConfigActions.EditSkinAction){
    return appConfig.setIn(['skin'], action.skinId);
  }

  _addLayout(appConfig: IMAppConfig, layoutJson: IMLayoutJson): IMAppConfig{
    if(appConfig.layouts && appConfig.layouts[layoutJson.id]){
      return appConfig;
    }

    appConfig = appConfig.setIn(['layouts', layoutJson.id], layoutJson);
    return appConfig;
  }

  _removeViewContent(appConfig: IMAppConfig, viewId: string): IMAppConfig{
    if(appConfig.widgets){
      appConfig = appConfig.set('widgets', appConfig.widgets.without.apply(appConfig.widgets, appConfig.views[viewId].widgets));
    }
    
    if(appConfig.views[viewId].sections){
      appConfig.views[viewId].sections.forEach(sectionId => {
        appConfig = this._removeSectionContent(appConfig, sectionId);
        appConfig = appConfig.set('sections', appConfig.sections.without(sectionId));
      });
    }

    appConfig = appConfig.set('layouts', appConfig.layouts.without(appConfig.views[viewId].layout));

    return appConfig;
  }

  _removePageContent(appConfig: IMAppConfig, pageId: string): IMAppConfig{
    if(appConfig.widgets){
      appConfig = appConfig.set('widgets', appConfig.widgets.without.apply(appConfig.widgets, appConfig.pages[pageId].widgets));
    }

    if(appConfig.pages[pageId].sections){
      appConfig.pages[pageId].sections.forEach(sectionId => {
        appConfig = this._removeSectionContent(appConfig, sectionId);

        appConfig = appConfig.set('sections', appConfig.sections.without(sectionId));
      });
    }

    appConfig = appConfig.set('layouts', appConfig.layouts.without(appConfig.pages[pageId].layout));
    return appConfig;
  }

  _removeSectionContent(appConfig: IMAppConfig, sectionId: string): IMAppConfig{
    if(appConfig.sections[sectionId].views){
      appConfig.sections[sectionId].views.forEach(viewId => {
        appConfig = this._removeViewContent(appConfig, viewId);
        appConfig = appConfig.set('views', appConfig.views.without(viewId));
      });
    }
    return appConfig;
  }

  getStoreKey(){
    return 'appState';
  }
}