import { React, ReactDOM } from 'jimu-core';
import { hasClass } from '../../../utils';

interface Props {
    rightBorder: boolean;
    active: boolean;
    style: string;
    icon: string;
    onChange: any;
}

export default class FontStyleItem extends React.Component<Props, any> {
    catchBlockType: string;
    constructor(props) {
        super(props);

    }

    handleClick(evt, style) {
        evt.nativeEvent.stopImmediatePropagation();
        let target = evt.target;
        if (hasClass(target, 'font-style-item')) {
            this.props.onChange(style);
        }
    }

    render() {

        const { active, style, icon, rightBorder } = this.props;

        let classNames = "font-style-item";
        classNames += ' ' + icon;
        if (active) {
            classNames += ' ' + 'active';
        }
        return <div className={classNames} style={{ backgroundImage: icon, borderRight: rightBorder ? '1px solid #D8D8D8' : 'none' }} onClick={(evt) => this.handleClick(evt, style)}></div>
    }
}
