import {IMAppConfig, AppMode, store, LayoutInfo, changeView} from 'jimu-core';
import {appStateActions} from './app-state-redux-store-extension';
import {appStateHistoryActions} from './app-state-history-extension';
import {builderActions} from './builder-state-store-extension';
import { WidgetSettingManager } from '..';

export function publishAppConfigChangeToApp(appConfig: IMAppConfig){
  publishEventToApp('to_app.app_config_changed', appConfig);
}

export function publishAppModeChangeToApp(appMode: AppMode){
  publishEventToApp('to_app.app_mode_changed', appMode);
}

export function publishPageChangeToApp(pageId: string){
  publishEventToApp('to_app.change_page', pageId);
}

export function publishViewChangeToApp(sectionId: string, viewId: string){
  publishEventToApp('to_app.change_view', {
    sectionId: sectionId,
    viewId: viewId
  });
}

export function publishCurrentSettingWidgetChangeToApp(widgetId: string){
  publishEventToApp('to_app.change_current_setting_widget', widgetId);
}

export function publishToggleWidgetHighlightToApp(widgetId: string, isHighlight: boolean){
  publishEventToApp('to_app.toggle_widget_highlight', {widgetId, isHighlight});
}

export function publishAddWidgetToApp(widgetId: string, layoutInfo: LayoutInfo){
  publishEventToApp('to_app.add_widget', {
    widgetId: widgetId,
    layoutInfo: layoutInfo
  });
}

export function publishAddSectionToApp(sectionId: string, layoutInfo: LayoutInfo){
  publishEventToApp('to_app.add_section', {
    sectionId: sectionId,
    layoutInfo: layoutInfo
  });
}

export function publishRemoveWidgetToApp(widgetId: string){
  publishEventToApp('to_app.remove_widget', {
    widgetId: widgetId,
  });
}

export function publishRemoveSectionToApp(sectionId: string){
  publishEventToApp('to_app.remove_section', {
    sectionId: sectionId,
  });
}

export function publishUpdateLayoutItemToApp(layoutInfo: LayoutInfo, layoutContent){
  publishEventToApp('to_app.update_layout_item', {
    layoutInfo: layoutInfo,
    layoutContent
  });
}

function publishEventToApp(action, data?){
  window._builderPubsub.publish(action, data);
}

export function listenAppEvents(){
  window._builderPubsub.subscribe('to_builder', function(evt, data){
    console.log(evt, data);
  });
  
  window._builderPubsub.subscribe('to_builder.app_state_changed', (evt, appState) => {
    if(!store.getState().appState){
      //the first time app get loaded, change app mode
      publishEventToApp('to_app.app_mode_changed', AppMode.Config);
    }

    if(!store.getState().appState || store.getState().appState.appConfig !== appState.appConfig){
      store.dispatch(appStateHistoryActions.InBuilderPutAppConfigIntoHistory(appState.appConfig));
    }
    store.dispatch(appStateActions.inAppAppStateChanged(appState));
  });
  
  window._builderPubsub.subscribe('to_builder.widget_config_changed', (evt, data) => {
    let widgetJson = store.getState().appState.appConfig.widgets[data.widgetId];

    store.dispatch(appStateActions.inBuilderEditAppEditWidget(widgetJson.set('config', data.widgetConfig)));
  });
  
  window._builderPubsub.subscribe('to_builder.current_widget_changed', (evt, widgetId) => {
    changeView('right-section', 'widget-setting-view');
    store.dispatch(builderActions.changeCurrentWidget(widgetId));
  });
  
  window._builderPubsub.subscribe('to_builder.widget_removed', (evt, widgetId) => {
    WidgetSettingManager.getInstance().deleteWidgetClass(widgetId);
    store.dispatch(builderActions.widgetRemoved(widgetId));
    store.dispatch(appStateActions.inBuilderEditAppRemoveWidget(widgetId));
  });

  window._builderPubsub.subscribe('to_builder.popup_choose_widget', (evt, layout) => {
    store.dispatch(builderActions.openChooseWidgetPopup(layout.layoutId, layout.layoutItemId));
  });
}
