import { RichUtils, Modifier, EditorState, ContentState, convertFromRaw, convertFromHTML } from 'draft-js';
import { colorStyleMap, colorStyles } from './editor-setting/color/colors';
export const isDescendant = (node, ancestor) => {
    if (document.contains) {
        return ancestor.contains(node);
    }
    try {
        while (node) {
            if (node == ancestor) {
                return true; // Boolean
            }
            node = node.parentNode;
        }
    } catch (e) { /* squelch, return false */ }
    return false;
};

export const getSelectedText = (editorState) => {
    var selectionState = editorState.getSelection();
    var anchorKey = selectionState.getAnchorKey();
    var currentContent = editorState.getCurrentContent();
    var currentContentBlock = currentContent.getBlockForKey(anchorKey);
    var start = selectionState.getStartOffset();
    var end = selectionState.getEndOffset();
    return currentContentBlock.getText().slice(start, end);
};

export const toggleColor = (color, editorState) => {

    const selection = editorState.getSelection();

    // Let's just allow one color at a time. Turn off all active colors.
    const nextContentState = Object.keys(colorStyleMap)
        .reduce((contentState, color) => {
            return Modifier.removeInlineStyle(contentState, selection, color)
        }, editorState.getCurrentContent());

    let nextEditorState = EditorState.push(
        editorState,
        nextContentState,
        'change-inline-style'
    );

    const currentStyle = editorState.getCurrentInlineStyle();

    // Unset style override for current color.
    if (selection.isCollapsed()) {
        nextEditorState = currentStyle.reduce((state, color) => {
            return RichUtils.toggleInlineStyle(state, color);
        }, nextEditorState);
    }

    // If the color is being toggled on, apply it.
    if (!currentStyle.has(color)) {
        nextEditorState = RichUtils.toggleInlineStyle(
            nextEditorState,
            color
        );
    }
    return nextEditorState;
};

export const toggleBlockType = (blockType, editorState) => {
    return RichUtils.toggleBlockType(
        editorState,
        blockType
    );
};

export const toggleInlineStyle = (inlineStyle, editorState) => {
    return RichUtils.toggleInlineStyle(
        editorState,
        inlineStyle
    );
};

export const getBlockType = (editorState) => {
    const selection = editorState.getSelection();
    const blockType = editorState
        .getCurrentContent()
        .getBlockForKey(selection.getStartKey())
        .getType();
    return blockType;
};

export const hasClass = (node, className) => {
    let classes = node.classList.value;
    return classes.indexOf(className) > -1;
};

export const getColorByEditorState = (editorState) => {
    let currentStyle = editorState.getCurrentInlineStyle();
    let color = '#000000';
    colorStyles.some((item) => {
        if (currentStyle.has(item.style)) {
            color = item.color;
            return true;
        }
    });
    return color;
};

export const getEditStateByWidgetConfig = ({ html, raw }) => {
    let editorState = EditorState.createEmpty();
    if (typeof raw !== 'undefined') {
        let rawContentState = JSON.parse(raw);
        const contentState = convertFromRaw(rawContentState);
        editorState = EditorState.createWithContent(contentState);
    } else if (typeof html !== 'undefined') {
        const blocksFromHTML = convertFromHTML(html);
        let contentState = ContentState.createFromBlockArray(
            blocksFromHTML.contentBlocks,
            blocksFromHTML.entityMap
        );
        editorState = EditorState.createWithContent(contentState);
    }
    return editorState;
};