import { React, AjaxState, SeamlessImmutable, ImmutableObject } from 'jimu-core';
import { IItem } from '@esri/arcgis-rest-common-types';
import { searchItems } from '@esri/arcgis-rest-items';
import { Input, Button, Form, FormGroup, InputGroup, InputGroupAddon } from 'jimu-ui';
import './css/item-chooser.scss';

interface Props {
  portalUrl: string;
  onSelect?: (item: IItem) => void;
  itemType: 'Web Map' | 'Web Scene'
}
interface State {
  state: AjaxState,
  items: IItem[],
  selectedItemId: string
}
export default class ItemChooser extends React.PureComponent<Props, State>{
  itemSearchInput: HTMLInputElement;

  constructor(props) {
    super(props);
    this.state = {
      state: null,
      items: [], //TODO if we wrap PortalItem as immutable, the properties are not availble, why?
      selectedItemId: ''
    }
  }
  componentDidMount() {
    this.setState({ 'state': AjaxState.Fetching });
    this.searchItem();
  }

  searchItem() {
    let s = this.itemSearchInput.value ? `type: "${this.props.itemType}" AND (${this.itemSearchInput.value})` : `type: "${this.props.itemType}"`;
    searchItems(s).then(items => {
      this.setState({
        state: AjaxState.Success,
        items: items.results
      });
    });
  }

  handleItemClick = (e, item) => {
    e.nativeEvent.stopImmediatePropagation();
    this.props.onSelect(item);
    this.setState({ selectedItemId: item.id });
  }

  onSearchInputKeyDown = (evt) => {
    if (evt.which === 13) {
      this.searchItem();
    }
  }

  render() {
    const { selectedItemId } = this.state;
    let items = <div className="item-list">
      {this.state.state === AjaxState.Fetching && <div>Fetching...</div>}
      {this.state.items.map((item: IItem) => {
        return <Item active={item.id === selectedItemId} key={item.id} item={item} portalUrl={this.props.portalUrl} onClick={this.handleItemClick}></Item>;
      })}
    </div>;
    return <div className="item-chooser">
      <InputGroup className="search">
        <Input onKeyDown={this.onSearchInputKeyDown} innerRef={dom => this.itemSearchInput = dom} />
        <InputGroupAddon addonType="append"><Button onClick={() => this.searchItem()} color="secondary">Search</Button></InputGroupAddon>
      </InputGroup>
      {items}
    </div>;
  }
}

const Item = ({ item, portalUrl, onClick, active }: { item: IItem, portalUrl: string, onClick: (evt: React.MouseEvent<HTMLDivElement>, item: IItem) => void, active: boolean }) => {

  return <div className="item" onClick={(evt) => onClick(evt, item)}>
    <img className={active ? 'selected' : ''} src={`${portalUrl}/sharing/rest/content/items/${item.id}/info/${item.thumbnail}`} />
    <div className="item-label">
      <div className="item-title">{item.title}</div>
    </div>
  </div>;
};