import { React, ReactDOM } from 'jimu-core';
import { Editor, convertToRaw } from 'draft-js';
import { styles, htmlStyles } from './customStyles'
import { stateToHTML } from 'draft-js-export-html';
import './css/style.scss';

interface Props {
    editorState: Draft.EditorState;
    editable: boolean;
    onChange: (html: string, raw: string, editorState: Draft.EditorState) => void;
}

interface State {
    editable: boolean;
}

export default class RichEditor extends React.Component<Props, State> {
    editorNode: Draft.Editor;
    editorPanelNode: HTMLDivElement;

    constructor(props) {
        super(props);
        this.state = {
            editable: false
        };
    }

    focusEditor = () => {
        if (this.editorNode) {
            this.enableEditor();
            this.editorNode.focus();
        }
    }

    handleEditorPanelClick = (evt) => {
        evt.nativeEvent.stopImmediatePropagation();
        this.focusEditor();
    }

    blurEditor = () => {
        if (this.editorNode) {
            this.editorNode.blur();
            this.disableEditor();
        }
    }

    onChange = (editorState) => {
        let contentState = editorState.getCurrentContent();
        let options = { inlineStyles: htmlStyles };
        let html = stateToHTML(contentState, options);
        let rawState = convertToRaw(contentState);
        let raw = JSON.stringify(rawState);
        this.props.onChange(html, raw, editorState);
    }

    isEnableEditor = () => this.state.editable;

    enableEditor() {
        this.setState({ editable: true });
    }

    disableEditor() {
        this.setState({ editable: false });
    }

    render() {
        const { editorState } = this.props;
        const { editable } = this.state;

        return (
            <div className={editable ? 'editor-root' : 'editor-root RichEditor-hidePlaceholder'}
                ref={ref => this.editorPanelNode = ref}>
                <Editor
                    customStyleMap={styles}
                    readOnly={!editable}
                    editorState={editorState}
                    onChange={this.onChange}
                    placeholder="Click to enter text."
                    ref={(rf) => this.editorNode = rf}
                />
            </div>
        );
    }
}