
import {WidgetJson, IMAppConfig, DataSourceJson, AppInfo, IMRuntimeInfos, ImmutableObject, SeamlessImmutable, LayoutInfo} from 'jimu-core';
import {State, IMState} from 'jimu-core';
import {extensionSpec, appConfigUtils} from 'jimu-core';

interface BuilderState{
  templateName: string;
  showChooseWidgetPopup: boolean;
  currentLayout: ImmutableObject<LayoutInfo>;
  currentPageId: string;
  currentSectionId: string;
  currentViewId: string;
  widgetsSettingRuntimeInfo?: IMRuntimeInfos;
}
type IMBuilderState = ImmutableObject<BuilderState>;
/**
 * To simplify the builder widgets communication, we store a shared state in store
 */
declare module 'jimu-core/lib/types/state'{
  interface State{
    builder?: IMBuilderState
  }
}
declare module 'jimu-core'{
  interface State{
    builder?: IMBuilderState
  }
}

enum ActionKeys{
  SelectTemplate = 'SELECT_TEMPLATE',
  OpenChooseWidgetPopup = 'OPEN_CHOOSE_WIDGET_POPUP',
  CloseChooseWidgetPopup = 'CLOSE_CHOOSE_WIDGET_POPUP',

  WidgetSettingClassLoaded = 'WIDGET_SETTING_CLASS_LOADED',
  ChangeCurrentWidget = 'CHANGE_CURRENT_WIDGET',
  WidgetRemoved = 'WIDGET_REMOVED',

  ChangeCurrentPage = 'CHANGE_CURRENT_PAGE',
  ChangeCurrentSection = 'CHANGE_CURRENT_SECTION',
  ChangeCurrentView = 'CHANGE_CURRENT_VIEW',
}

export interface SelectTemplateAction{
  type: ActionKeys.SelectTemplate,
  templateName: string;
}
export interface ShowChooseWidgetPopupAction{
  type: ActionKeys.OpenChooseWidgetPopup,
  layoutId: string;
  layoutItemId: string;
}
export interface CloseChooseWidgetPopupAction{
  type: ActionKeys.CloseChooseWidgetPopup,
}
export interface WidgetSettingClassLoadedAction{
  type: ActionKeys.WidgetSettingClassLoaded,
  widgetId: string;
}
export interface ChangeCurrentWidgetAction{
  type: ActionKeys.ChangeCurrentWidget,
  widgetId: string;
}
export interface WidgetRemovedAction{
  type: ActionKeys.WidgetRemoved,
  widgetId: string;
}
export interface ChangeCurrentPageAction{
  type: ActionKeys.ChangeCurrentPage,
  pageId: string;
}
export interface ChangeCurrentSectionAction{
  type: ActionKeys.ChangeCurrentSection,
  sectionId: string;
}
export interface ChangeCurrentViewAction{
  type: ActionKeys.ChangeCurrentView,
  viewId: string;
}

type ActionTypes = SelectTemplateAction | ShowChooseWidgetPopupAction | CloseChooseWidgetPopupAction | WidgetSettingClassLoadedAction | ChangeCurrentWidgetAction |
  WidgetRemovedAction | ChangeCurrentPageAction | ChangeCurrentSectionAction | ChangeCurrentViewAction;

export {ActionKeys as BuilderStateActionTypes}

let actions = {
  selectTemplate: (templateName: string): SelectTemplateAction => {
    return {
      type: ActionKeys.SelectTemplate,
      templateName: templateName
    }
  },

  openChooseWidgetPopup: (layoutId: string, layoutItemId: string): ShowChooseWidgetPopupAction => {
    return {
      type: ActionKeys.OpenChooseWidgetPopup,
      layoutId: layoutId,
      layoutItemId: layoutItemId
    }
  },

  closeChooseWidgetPopup: (): CloseChooseWidgetPopupAction => {
    return {
      type: ActionKeys.CloseChooseWidgetPopup,
    }
  },

  widgetSettingClassLoaded: (widgetId: string) => {
    return {
      type: ActionKeys.WidgetSettingClassLoaded,
      widgetId: widgetId
    };
  },

  changeCurrentWidget: (widgetId: string) => {
    return {
      type: ActionKeys.ChangeCurrentWidget,
      widgetId: widgetId
    };
  },

  widgetRemoved: (widgetId: string) => {
    return {
      type: ActionKeys.WidgetRemoved,
      widgetId: widgetId
    };
  },

  changeCurrentPage: (pageId: string): ChangeCurrentPageAction => {
    return {
      type: ActionKeys.ChangeCurrentPage,
      pageId: pageId
    };
  },

  changeCurrentSection: (sectionId: string): ChangeCurrentSectionAction => {
    return {
      type: ActionKeys.ChangeCurrentSection,
      sectionId: sectionId
    };
  },

  changeCurrentView: (viewId: string): ChangeCurrentViewAction => {
    return {
      type: ActionKeys.ChangeCurrentView,
      viewId: viewId
    };
  },

};

export {actions as builderActions, ActionKeys as builderActionKeys}

export default class BuilderStateReduxStoreExtension implements extensionSpec.ReduxStoreExtension{
  id = 'builder-state-redux-store-extension';

  getActions(){
    return Object.keys(ActionKeys).map(k => ActionKeys[k]);
  }

  getInitLocalState(){
    return {
      templateName: null,
      showChooseWidgetPopup: false,
      widgetsSettingRuntimeInfo: {}
    };
  }

  getReducer(){
    return (builderState: IMBuilderState, action: ActionTypes, builderFullState: IMState) => {
      let s;
      switch(action.type){
        case ActionKeys.SelectTemplate:
          return builderState.set('templateName', action.templateName);
        case ActionKeys.OpenChooseWidgetPopup:
          return builderState.set('showChooseWidgetPopup', true)
          .set('currentLayout', SeamlessImmutable({
            layoutId: action.layoutId,
            layoutItemId: action.layoutItemId
          }));
        case ActionKeys.CloseChooseWidgetPopup:
          return builderState.set('showChooseWidgetPopup', false);
        
        case ActionKeys.WidgetSettingClassLoaded:
          return builderState.setIn(['widgetsSettingRuntimeInfo', action.widgetId, 'isClassLoaded'], true);
        case ActionKeys.ChangeCurrentWidget:
          let info = builderState.widgetsSettingRuntimeInfo;
          Object.keys(info).forEach(wId => {
            info = info.setIn([wId, 'isSetting'], false);
          });
          info = info.setIn([action.widgetId, 'isSetting'], true);
          return builderState.set('widgetsSettingRuntimeInfo', info);
        case ActionKeys.WidgetRemoved:
          return builderState.set('widgetsSettingRuntimeInfo', builderState.widgetsSettingRuntimeInfo.without(action.widgetId));

        case ActionKeys.ChangeCurrentPage:
          return builderState.set('currentPageId', action.pageId);
        case ActionKeys.ChangeCurrentSection:
          return builderState.set('currentSectionId', action.sectionId);
        case ActionKeys.ChangeCurrentView:
          return builderState.set('currentViewId', action.viewId);

        default:
          return builderState;
      }
    }
  }

  getStoreKey(){
    return 'builder';
  }
}