import { React, ReactDOM } from 'jimu-core';
import ColorPanel from './color-panel';
import { isDescendant, getColorByEditorState } from '../../utils';
import './css/style.scss';

interface Props {
    onSelected: any;
    editorState: Draft.EditorState;
}

interface State {
    show: boolean;
}

export default class Picker extends React.Component<Props, State> {
    handler: any;
    pickerNode: HTMLDivElement;
    pickerBtnNode: HTMLDivElement;

    constructor(props) {
        super(props);

        this.state = {
            show: false
        };

        this.onColorSelected = this.onColorSelected.bind(this);
        this.toggleColorPanel = this.toggleColorPanel.bind(this);
        this.hideColorPanel = this.hideColorPanel.bind(this);
    }

    componentDidMount() {
        this.handler = (evt) => {
            if (!this.pickerNode) {
                return;
            }
            if (this.pickerNode && !isDescendant(evt.target, this.pickerNode)) {
                this.hideColorPanel();
            }
        }
        document.addEventListener("click", this.handler);
    }

    componentWillUnmount() {
        document.removeEventListener("click", this.handler);
        this.handler = null;
    }

    onColorSelected(color, style) {
        this.props.onSelected(color, style)
    }

    hideColorPanel() {
        this.setState({ show: false })
    }

    toggleColorPanel() {
        this.setState({
            show: !this.state.show
        })
    }

    render() {
        const { show } = this.state;
        const { editorState } = this.props;
        const color = getColorByEditorState(editorState);


        let colorPanel = <div></div>;
        if (this.pickerBtnNode && this.pickerNode) {
            colorPanel = <ColorPanel editorState={editorState} show={show} target={this.pickerNode} container={this.pickerNode} onSelected={this.onColorSelected} />
        }
        return (
            <div className="color-picker" ref={(rf) => this.pickerNode = rf}>
                <div className="picker-btn" ref={(rf) => this.pickerBtnNode = rf} style={{ backgroundColor: color }} onClick={this.toggleColorPanel}></div>
                <div className="picker-panel">{colorPanel}</div>
            </div >
        )
    }
}