//import { IMAttributesJson } from './../../jimu-core/lib/types/app-config';
import {IMWidgetJson, IMPageJson, IMSectionJson, IMViewJson, IMAppConfig, IMDataSourceJson, IMLayoutJson, ContainerType, LayoutInfo, IMAttributesJson} from 'jimu-core';

export enum ActionKeys{
  //edit app actions
  InBuilderEditAppEditWidget = 'IN_BUILDER_EDIT_APP_Edit_WIDGET',
  InBuilderEditAppAddWidget = 'IN_BUILDER_EDIT_APP_ADD_WIDGET',
  InBuilderEditAppRemoveWidget = 'IN_BUILDER_EDIT_APP_REMOVE_WIDGET',

  InBuilderEditAppEditPage = 'IN_BUILDER_EDIT_APP_Edit_PAGE',
  InBuilderEditAppAddPage = 'IN_BUILDER_EDIT_APP_ADD_PAGE',
  InBuilderEditAppRemovePage = 'IN_BUILDER_EDIT_APP_REMOVE_PAGE',

  InBuilderEditAppAddSection = 'IN_BUILDER_EDIT_APP_ADD_SECTION',
  InBuilderEditAppRemoveSection = 'IN_BUILDER_EDIT_APP_REMOVE_SECTION',

  InBuilderEditAppAddView = 'IN_BUILDER_EDIT_APP_ADD_VIEW',
  InBuilderEditAppEditView = 'IN_BUILDER_EDIT_APP_EDIT_VIEW',
  InBuilderEditAppRemoveView = 'IN_BUILDER_EDIT_APP_REMOVE_VIEW',

  InBuilderEditAppAddDataSource = 'IN_BUILDER_EDIT_APP_ADD_DATA_SOURCE',
  InBuilderEditAppRemoveDataSource = 'IN_BUILDER_EDIT_APP_REMOVE_DATA_SOURCE',

  InBuilderEditAppEditAttributes = 'IN_BUILDER_EDIT_APP_EDIT_ATTRIBUTES',

  InBuilderEditAppEditSkin = 'IN_BUILDER_EDIT_APP_EDIT_SKIN'
}

export type ActionTypes = EditWidgetAction | AddWidgetAction | RemoveWidgetAction | AddPageAction | EditPageAction | RemovePageAction |
  AddSectionAction | RemoveSectionAction | AddViewAction | RemoveViewAction | EditViewAction |
  AddDataSourceAction | RemoveDataSourceAction | EditAttributesAction | EditSkinAction;

export interface EditWidgetAction{
  type: ActionKeys.InBuilderEditAppEditWidget;
  widgetJson: IMWidgetJson;
}
export interface AddWidgetAction{
  type: ActionKeys.InBuilderEditAppAddWidget;
  widgetJson: IMWidgetJson;
  layoutInfo: LayoutInfo;
}
export interface RemoveWidgetAction{
  type: ActionKeys.InBuilderEditAppRemoveWidget;
  widgetId: string;
}

export interface AddPageAction{
  type: ActionKeys.InBuilderEditAppAddPage;
  pageJson: IMPageJson;
  pageLayoutJson?: IMLayoutJson;
}
export interface EditPageAction{
  type: ActionKeys.InBuilderEditAppEditPage;
  pageJson: IMPageJson;
}
export interface RemovePageAction{
  type: ActionKeys.InBuilderEditAppRemovePage;
  pageId: string;
}

export interface AddSectionAction{
  type: ActionKeys.InBuilderEditAppAddSection;
  sectionJson: IMSectionJson;
  layoutInfo: LayoutInfo;
}
export interface RemoveSectionAction{
  type: ActionKeys.InBuilderEditAppRemoveSection;
  sectionId: string;
}

export interface AddViewAction{
  type: ActionKeys.InBuilderEditAppAddView;
  viewJson: IMViewJson;
  sectionId: string;
  viewLayoutJson?: IMLayoutJson;
}
export interface EditViewAction{
  type: ActionKeys.InBuilderEditAppEditView;
  viewJson: IMViewJson;
}
export interface RemoveViewAction{
  type: ActionKeys.InBuilderEditAppRemoveView;
  sectionId: string;
  viewId: string;
}

export interface AddDataSourceAction{
  type: ActionKeys.InBuilderEditAppAddDataSource;
  dataSourceJson: IMDataSourceJson;
}
export interface RemoveDataSourceAction{
  type: ActionKeys.InBuilderEditAppRemoveDataSource;
  dataSourceId: string;
}
export interface EditAttributesAction{
  type: ActionKeys.InBuilderEditAppEditAttributes;
  attributesJson: IMAttributesJson;
}

export interface EditSkinAction{
  type: ActionKeys.InBuilderEditAppEditSkin;
  skinId: string;
}

export const actions = {
  inBuilderEditAppEditWidget: (widgetJson: IMWidgetJson): EditWidgetAction => {
    return {
      type: ActionKeys.InBuilderEditAppEditWidget,
      widgetJson: widgetJson
    };
  },
  inBuilderEditAppAddWidget: (widgetJson: IMWidgetJson, layoutInfo: LayoutInfo): AddWidgetAction => {
    return {
      type: ActionKeys.InBuilderEditAppAddWidget,
      widgetJson: widgetJson,
      layoutInfo: layoutInfo
    };
  },
  inBuilderEditAppRemoveWidget: (widgetId: string): RemoveWidgetAction => {
    return {
      type: ActionKeys.InBuilderEditAppRemoveWidget,
      widgetId: widgetId
    };
  },
  inBuilderEditAppEditPage: (pageJson: IMPageJson): EditPageAction => {
    return {
      type: ActionKeys.InBuilderEditAppEditPage,
      pageJson: pageJson
    };
  },
  inBuilderEditAppAddPage: (pageJson: IMPageJson, pageLayoutJson?: IMLayoutJson): AddPageAction => {
    return {
      type: ActionKeys.InBuilderEditAppAddPage,
      pageJson: pageJson,
      pageLayoutJson: pageLayoutJson
    };
  },
  inBuilderEditAppRemovePage: (pageId: string): RemovePageAction => {
    return {
      type: ActionKeys.InBuilderEditAppRemovePage,
      pageId: pageId
    };
  },

  inBuilderEditAppAddSection: (sectionJson: IMSectionJson, layoutInfo: LayoutInfo): AddSectionAction => {
    return {
      type: ActionKeys.InBuilderEditAppAddSection,
      sectionJson: sectionJson,
      layoutInfo: layoutInfo
    };
  },
  inBuilderEditAppRemoveSection: (sectionId: string): RemoveSectionAction => {
    return {
      type: ActionKeys.InBuilderEditAppRemoveSection,
      sectionId: sectionId,
    };
  },
  
  inBuilderEditAppEditView: (viewJson: IMViewJson): EditViewAction => {
    return {
      type: ActionKeys.InBuilderEditAppEditView,
      viewJson: viewJson
    };
  },
  inBuilderEditAppAddView: (viewJson: IMPageJson, sectionId: string, viewLayoutJson?: IMLayoutJson): AddViewAction => {
    return {
      type: ActionKeys.InBuilderEditAppAddView,
      viewJson: viewJson,
      sectionId: sectionId,
      viewLayoutJson: viewLayoutJson
    };
  },
  inBuilderEditAppRemoveView: (viewId: string, sectionId: string): RemoveViewAction => {
    return {
      type: ActionKeys.InBuilderEditAppRemoveView,
      viewId: viewId,
      sectionId: sectionId
    };
  },

  inBuilderEditAppAddDataSource: (dataSourceJson: IMDataSourceJson): AddDataSourceAction => {
    return {
      type: ActionKeys.InBuilderEditAppAddDataSource,
      dataSourceJson: dataSourceJson
    };
  },

  inBuilderEditAppRemoveDataSource: (dataSourceId: string): RemoveDataSourceAction => {
    return {
      type: ActionKeys.InBuilderEditAppRemoveDataSource,
      dataSourceId: dataSourceId
    };
  },

  InBuilderEditAppEditAttributes: (attributesJson: IMAttributesJson): EditAttributesAction => {
    return {
      type: ActionKeys.InBuilderEditAppEditAttributes,
      attributesJson: attributesJson
    };
  },

  InBuilderEditAppEditSkin: (skinId: string): EditSkinAction => {
    return {
      type: ActionKeys.InBuilderEditAppEditSkin,
      skinId: skinId
    }
  },
};